import { CurrencyPipe } from '@angular/common';
import { Component, OnInit } from '@angular/core';
import { I18nConfig } from '@config/i18n.config';
import { EmptyCaseException } from '@nequi/commons/exceptions';
import { CardInteractor } from '@nequi/features/card/core';
import { AccountType } from '@nequi/features/commons/core';
import {
  NavigatorProvider,
  TranslateProvider,
  UserInfoProvider
} from '@nequi/features/commons/providers';
import { MoneyManagementInteractor, PocketType } from '@nequi/features/money-management/core';
import { BottomToTopTransition, ToastService } from '@nequi/ui';

import {
  BackpackMoneyPartitionConfig,
  CardAlternativeAmount,
  PocketAlternativeAmount
} from './backpack-money-partition.config';
import {
  BackpackMoneyPartitionViewModel,
  PocketAlternativeAmountTranslator
} from './backpack-money-partition.view-model';

@Component({
  selector: 'app-backpack-money-partition',
  templateUrl: './backpack-money-partition.page.html',
  styleUrls: ['./backpack-money-partition.page.scss'],
})
export class BackpackMoneyPartitionPage implements OnInit {
  public config = BackpackMoneyPartitionConfig;
  public viewModel: BackpackMoneyPartitionViewModel;
  public cardItems = [];
  public cardTapArrow;
  public isTapeable: boolean;

  private altAmtByPocketType: Map<PocketType, PocketAlternativeAmount>;
  private altAmtTranslatorByPocketType: Map<PocketType, PocketAlternativeAmountTranslator>;

  constructor(
    private navigatorProvider: NavigatorProvider,
    private translateProvider: TranslateProvider,
    private moneyManagementInteractor: MoneyManagementInteractor,
    private toastService: ToastService,
    private userInfoProvider: UserInfoProvider,
    private cardInteractor: CardInteractor,
    private currencyPipe: CurrencyPipe
  ) {
    this.translateProvider.loadModule(I18nConfig.modules.BACKPACK);
  }

  public ngOnInit() {
    this.viewModel = new BackpackMoneyPartitionViewModel();
    this.cardItems = this.translateProvider.get(this.config.cardItems);
    this.cardTapArrow = this.translateProvider.get(this.config.cardTapArrow);
    this.isTapeable = true;
  }

  public ionViewWillEnter() {
    this.initialize();
  }

  public ionViewWillLeave() {
    this.viewModel.isLoadingMoneyData = true;
    this.viewModel.loadingError = '';
  }

  public closeMoneyDetail() {
    this.navigatorProvider.back();

    // TODO: A la espera de solución de BUG - https://github.com/ionic-team/ionic-framework/issues/20448
    // this.navigatorProvider.navigateBack(this.config.routes.home, {
    //   animated: true,
    //   animation: (baseEl, opts) => backpackToHomeTransition(baseEl, opts),
    // });
  }

  public goToModule(item) {
    if (!item.value[1].tapeable) {
      return;
    }

    this.navigatorProvider.navigateForward(item.value[1].link);
  }

  public async goToNequiCeiling() {
    this.navigatorProvider.navigateRoot(this.config.routes.ceiling, {
      animated: true,
      animation: (baseEl, opts) => BottomToTopTransition(baseEl, opts),
    });
  }

  public get displayCeilingComponent() {
    return this.userInfoProvider.getAccount().type !== AccountType.SAVINGS;
  }

  public async initialize() {
    try {
      this.viewModel.isLoadingMoneyData = true;
      this.viewModel.loadingError = '';

      const pockets = await this.moneyManagementInteractor.getPockets([
        PocketType.POCKET,
        PocketType.CARD,
        PocketType.GOAL,
        PocketType.AVAILABLE,
        PocketType.MATTRESS,
      ]);

      await this.initAlternativeAmounts();
      this.viewModel.setAmounts(pockets, {
        amountByType: this.altAmtByPocketType,
        translatorByType: this.altAmtTranslatorByPocketType,
      });

      this.viewModel.isLoadingMoneyData = false;
    } catch (error) {
      if (error instanceof EmptyCaseException) {
        this.viewModel.setAmounts();
        this.toastService.showError(this.translateProvider.get(this.config.i18n.defaultError));

        return;
      }

      this.viewModel.loadingError = error.message;
    }
  }

  private async initAlternativeAmounts() {
    this.altAmtByPocketType = new Map();
    this.altAmtTranslatorByPocketType = new Map();
    await this.initCardAlternativeAmounts();
  }

  private async initCardAlternativeAmounts() {
    try {
      const card = await this.cardInteractor.get();

      if (!card.useFree) {
        return;
      }

      const alternativeAmount =
        card.balance > 0
          ? CardAlternativeAmount.BALANCE_PLUS_AVAILABLE
          : CardAlternativeAmount.AVAILABLE;

      this.altAmtByPocketType.set(PocketType.CARD, alternativeAmount);

      this.altAmtTranslatorByPocketType.set(PocketType.CARD, (i18nKey) =>
        this.translateProvider.get(i18nKey, {
          balance: this.currencyPipe.transform(card.balance),
        })
      );
    } catch {}
  }
}
