import { Pocket, PocketType } from '@nequi/features/money-management/core';
import { isTypeOf } from '@nequi/utils/types';

import {
  BackpackMoneyPartitionConfig,
  PocketAlternativeAmount
} from './backpack-money-partition.config';

const getTotalsByPocketType = (totals: Record<PocketType, number>, pocket: Pocket) => {
  if (!totals[pocket.type]) {
    totals[pocket.type] = 0;
  }

  totals[pocket.type] += pocket.amount;

  return totals;
};

export type PocketAlternativeAmountTranslator = (i18nKey: string) => string;

export interface PocketsAlternativeAmountsHandler {
  amountByType: Map<PocketType, PocketAlternativeAmount>;
  translatorByType: Map<PocketType, PocketAlternativeAmountTranslator>;
}

export class BackpackMoneyPartitionViewModel {
  public config = BackpackMoneyPartitionConfig;
  public module = { ...this.config.module };
  public loadingError: string;
  public isLoadingMoneyData: boolean;
  public totalAmount = null;
  public arrayPockets = [];

  public setAmounts(pockets?: Pocket[], handler?: PocketsAlternativeAmountsHandler) {
    if (!pockets || !handler) {
      return;
    }

    const totals = pockets.reduce(getTotalsByPocketType, {} as Record<PocketType, number>);

    this.totalAmount = pockets.reduce((total, pocket) => (total = total + pocket.amount), 0);

    this.module.available.amount = totals[PocketType.AVAILABLE] ?? 0;

    this.module.mattress.amount = totals[PocketType.MATTRESS];
    this.module.mattress.isEmpty = this.isValueEmpty(this.module.mattress.amount);

    this.module.pockets.amount = totals[PocketType.POCKET];
    this.module.pockets.isEmpty = this.isValueEmpty(this.module.pockets.amount);

    this.module.goals.amount = totals[PocketType.GOAL];
    this.module.goals.isEmpty = this.isValueEmpty(this.module.goals.amount);

    this.module.card.amount = totals[PocketType.CARD];
    this.module.card.isEmpty = this.isValueEmpty(this.module.card.amount);

    this.arrayPockets = Object.entries(this.module);
    this.sortPockets();
    this.handlePocketsAlternativeAmounts(handler);
  }

  public setIcons(value) {
    return value.isEmpty ? value.emptyCase.icon : value.icon;
  }

  private isValueEmpty(amount: number) {
    const isNumber = isTypeOf<number>(amount, Number);

    return (isNumber && amount < 0) || (!isNumber && !amount);
  }

  private sortPockets() {
    this.arrayPockets.sort((a, b) => a[1].isEmpty - b[1].isEmpty || a[1].priority - b[1].priority);
  }

  private handlePocketsAlternativeAmounts(handler: PocketsAlternativeAmountsHandler) {
    this.arrayPockets.forEach((pocket) => {
      if (pocket[1].alternativeAmount) {
        const type = pocket[1].type;
        const amount = handler.amountByType.get(type);
        const i18nKey = pocket[1].alternativeAmount[amount];
        const translator = handler.translatorByType.get(type);

        pocket[1].alternativeAmountTranslation = translator?.(i18nKey);
      }
    });
  }
}
