import { NgModule } from '@angular/core';
import { RouterModule, Routes } from '@angular/router';
import { IonicModule } from '@ionic/angular';
import { ModuleWithProvidersFactory, ORIGIN_FLOW, OriginFlow } from '@nequi/features/commons';
import { BottomToTopModalAnimation, TopToBottomModalAnimation } from '@nequi/ui';

import {
  BackpackMoneyPartitionPage
} from './backpack-money-partition/backpack-money-partition.page';

const routes: Routes = [
  {
    path: 'money-partition',
    component: BackpackMoneyPartitionPage,
  },
  {
    path: 'choose-biometry',
    loadChildren: () =>
      import('@nequi/features/security/biometry/presentation/choose-biometry').then(
        (m) => m.ChooseBiometryModule
      ),
  },
  {
    path: 'data-treatment',
    loadChildren: () =>
      import('@nequi/features/data-treatment/presentation').then(
        (m) =>
          new ModuleWithProvidersFactory(
            m.FeaturesDataTreatmentModule.withConfig([
              { provide: ORIGIN_FLOW, useValue: OriginFlow.BACKPACK },
            ])
          )
      ),
  },
];

@NgModule({
  imports: [
    RouterModule.forChild(routes),
    IonicModule.forRoot({
      modalEnter: BottomToTopModalAnimation,
      modalLeave: TopToBottomModalAnimation,
    }),
  ],
  exports: [RouterModule],
})
export class BackpackRoutingModule {}
